body {
  margin: 0;
  font-family: "Nanum Gothic", sans-serif;
  min-width: 700px;
  background-color: #f4f2f4;
  color: #f4f2f4;
}
.navbar {
  width: 100%;
  height: 50px;
  top: 0;
  margin-bottom: 30px;
  background-color: #947d94;
  color: #f4f2f4;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}
h4 {
  margin: auto 20px;
  font-size: 22px;
}
.section {
  height: 85vh;
  min-height: 400px;
  border: 2px solid #ded8de;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

.top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 50px;
  border-bottom: 2px solid #ded8de;
}
.option {
  flex: 1;
  border-right: 1px solid #ded8de;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #947d94be;
  cursor: pointer;
}
.option:hover {
  background-color: #947d94;
}

.main-section {
  display: grid;
  width: 90%;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  margin: 10px 0;
}
.left {
  width: 600px;
  background-image: url("body/body.png");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.right {
  width: 600px;
  overflow-y: hidden;
}

.center {
  overflow-y: scroll;
  height: 78vh;
  min-height: 400px;
  display: flex;
  align-items: center;
  flex-direction: column;
  /* height: 100%; */
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background-color: whitesmoke;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #ded8de;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #947d94;
}
.element {
  border: 2px solid #ded8dea1;
  /* width: 100px;
  height: 100px; */
  height: 180px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.element:hover {
  background-color: whitesmoke;
}

.body-part_img {
  width: 100%;
  max-width: 140px;
  max-height: 180px;
  object-fit: contain;
}
.element img {
  object-fit: contain;
}

.part-container {
  height: 50%;
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.part {
  /* background-color: red; */
  width: 50%;
  display: flex;
  align-items: center;
  flex-direction: column;
  min-height: 90px;
}
.nose {
  /* background-color: orange; */
  z-index: 2;
  transform: translateY(-10px);
}

.eyes img {
  max-width: 400px;
  height: 100%;
  object-fit: contain;
}
.nose img {
  max-width: 150px;
  height: 100%;
  object-fit: contain;
}
.mouth img {
  max-width: 350px;
  height: 100%;
  object-fit: contain;
}
.hair img {
  width: 90%;
  height: 100%;
  object-fit: contain;
  transform: scale(0.6);
}

.ear {
  position: absolute;
  height: 90px;
  z-index: 4;
  width: 70px;
}
.ear img {
  object-fit: contain;
  width: 100%;
}
.left-ear {
  transform: translateX(-220px) scale(-1, 1);
}
.right-ear {
  transform: translateX(220px);
}

.selected {
  background-color: #947d94;
}

@media screen and (max-width: 1025px) {
  .main-section {
    grid-template-columns: 1fr 1fr;
  }
  .left {
    width: 500px;
  }
  .right {
    width: 350px;
  }
  .left-ear {
    transform: translateX(-170px) scale(-1, 1);
  }
  .right-ear {
    transform: translateX(170px);
  }
}
@media screen and (max-width: 769px) {
  .main-section {
    grid-template-columns: 1fr;
  }
  .left {
    width: 500px;
  }
  .right {
    width: 250px;
  }
  .left-ear {
    transform: translateX(-165px) scale(-1, 1);
  }
  .right-ear {
    transform: translateX(165px);
  }
  img {
    transform: scale(0.8);
  }
  .part-container {
    width: 70%;
    justify-self: center;
  }
}
